@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
body {
  margin: 0;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 960px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.utility-label p {
  margin: 10px 0 10px;
}

.utility-label input {
  padding: 5px;
  font-size: 16px;
  width: 300px;
  border-radius: 4px;
}

.utility-label textarea {
  padding: 5px;
  font-size: 16px;
  width: 400px;
  height: 100px;
  border-radius: 4px;
}

.utility-component-block {
  border-bottom: 1px solid #666;
  padding-bottom: 10px;
}

.utility-component-block h2 {
  text-decoration: underline;
}

.ComponentBlockSelector {
  margin-top: 15px;
  display: flex;
}

.ComponentBlockSelector-select-holder {
  width: 300px;
}

.ComponentBlockSelector button {
  margin-left: 10px;
  background: #308dc6;
  color: #fff;
  font-size: 16px;
  border-radius: 4px;
  padding: 0 15px;
}

.ComponentBlockSelector-header {
  margin: 10px 0;
}

.MainLayout-container {
  display: flex;
}

.MainLayout-container > div {
  padding: 10px;
  width: 50%;
}

